body {
    font-family: sans-serif;
    margin: 0;
}

a{
    text-decoration: none;
    color: inherit;
}

.container{
    min-height: 100vh;
    display: grid;
    grid-template-rows: 100px auto auto;
}

/*.container>*{
    padding: 0 calc(50% - 600px);
}
*/
.cards{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2em;
}

@media (max-width: 1260px) {
    /*.container>*{
        padding: 0 30px;
    }*/
    .cards{
        grid-template-columns: repeat(3, 1fr);
    }
}

.empty-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 100%;
    justify-content: center;
}
/* .empty-block>*:not(svg) {
    font-size: 1rem;
} */
.empty-block svg {
    font-size: 4rem;
    fill: #dedede;
}
.empty-block .btn {
    border: 1px solid #dedede;
    border-radius: 18px;
    display: flex;
    padding: 6px 12px;
}