.modal-container {
    position:  fixed;
    background-color: #2228;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: none;
    justify-content: center;
    align-items: center;
    z-index: 100;
}

.modal {
    width: 300px;
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 2px 0 #0006, 0 0 8px 2px #0004; 
    display: grid;
    gap: 20px;
    position: relative;
}

.modal h2 {
    margin: 0;
    text-align: center;
}

.modal-close{
    width: 20px;
    height: 20px;
    background-color: #222;
    border-radius: 50%;
    border: 2px solid;
    box-sizing: border-box;
    position: absolute;
    top: -8px;
    right: -8px;
    cursor: pointer;
}

.modal-close:hover {
    background-color: rgb(243, 231, 5);
}

form{
    display: grid;
    gap: 10px;

}

input{
    flex: 1;
    height: 30px;
    box-sizing: border-box;
    padding: 6px 12px;
    border-radius: 10px;
    border:  1px solid silver;
    font-size: 16px;
    outline: none;
}

input:focus{
    border-color: rgb(243, 231, 5);
}

button.btn{
    border: none;
    height: 30px;
    border-radius: 10px;
    background-color: rgb(243, 231, 5);
}
button.link{
    background-color: transparent;
    border: 1px solid rgb(243, 231, 5);
    box-sizing: border-box;
}